body {
  font-family: 'Proxima Nova';
}

.container {
  max-width: 1200px;
  width: 100%;
  position: relative;
  height: 100%;
}

.row {
  height: 100%;
}

h2 {
  font-size: 24px;
  font-weight: 600;
  letter-spacing: 1.8px;
  color: #000000;
  text-transform: uppercase;
}

.btn {
  text-decoration: none;
  border: 1px solid #ffffff;
  background: transparent;
  color: #ffffff;
  padding: 15px 40px;
  height: 100%;
  letter-spacing: 1.6px;
  font-weight: 600;
  text-align: center;
  cursor: pointer;
  transition: all 0.3s;
  text-transform: lowercase;
}

.btn:hover {
  background: #ffffff;
  color: #2d6965;
}

.btn--white {
  background: #ffffff;
  width: 170px;
  color: #2d6965;
  margin-right: 15px;
}

.btn--white:hover {
  background: #2d6965;
  color: #ffffff;
  border-color: #2d6965;
}

.btn--thank {
  display: inline-block;
  color: #2d6965;
  border: solid 2px #2d6965;
}

.btn--thank:hover {
  background: #2d6965;
  color: #ffffff;
  border-color: #2d6965;
}

.social {
  position: fixed;
  max-width: 1200px;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  bottom: 70px;
  display: flex;
  z-index: 10;
}

.social svg {
  width: 32px;
  height: 32px;
  fill: #fff;
  margin-right: 10px;
  transition: fill 0.3s;
}

.social svg:hover {
  fill: #2d6965;
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
  -webkit-appearance: none;
  margin: 0;
}

.checkbox {
  vertical-align: top;
  width: 25px;
  height: 25px;
  margin-bottom: 175px;
}

.checkbox.invalid + .label:before {
  border: 1px solid red;
}

.checkbox.invalid + .label + .label-for {
  color: red;
}

.checkbox.invalid + .label + .label-for a {
  color: red;
}

.checkbox + .label {
  cursor: pointer;
}

.checkbox:not(checked) {
  position: absolute;
  opacity: 0;
}

.checkbox:not(checked) + .label {
  position: relative;
  padding: 0 0 0 40px;
}

.checkbox:not(checked) + .label:before {
  content: '';
  position: absolute;
  top: -4px;
  left: 0;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: transparent;
  border: 1px solid #2d6965;
}

.checkbox:not(checked) + .label:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 8px;
  width: 15px;
  height: 9px;
  border: 3px solid #ffffff;
  border-top: none;
  border-right: none;
  transform: rotate(-45deg);
  transition: all .2s;
  opacity: 0;
  background: transparent !important;
}

.checkbox:checked + .label:before {
  background: #2d6965;
}

.checkbox:checked + .label:after {
  opacity: 1;
}

.checkbox:checked + .label + .label-for h3, .checkbox:checked + .label + .label-for p {
  color: #2d6965;
}

.label-for {
  font-size: 16px;
  color: #333333;
}

.label-for a {
  color: #333333;
}

.label-for {
  display: inline-block;
  cursor: pointer;
  position: relative;
  top: 2px;
}

.label-for h3 {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

.label-for img {
  width: 45px;
  margin-right: 15px;
}

#preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #ffffff;
  z-index: 1000;
}

#preloader .svg-wrapper {
  height: 150px;
  margin: 0 auto;
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  width: 150px;
}

#preloader .animate {
  fill: transparent;
  stroke-dasharray: 300;
  stroke-dashoffset: 600;
  stroke-width: 5px;
  stroke: #2d6965;
}

#preloader svg {
  width: 150px;
  height: 150px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s;
}

#preloader svg path, #preloader svg line, #preloader svg polyline {
  stroke: #000;
  stroke-width: 2px;
  transition: all 0.3s;
}

#preloader svg path {
  fill: transparent;
}

img {
  opacity: 1;
  transition: opacity 0.3s;
}

img[data-src] {
  opacity: 0;
}
